.main-not-found {
  padding-top: 80px;
  flex-direction: column;
  display: flex;
  justify-content: center;
  align-items: center;

  .home-link {
    font-size: 20px;
    color: white;
    font-family: "Do Hyeon", sans-serif;
    padding-bottom: 10px;
  }

  .message {
    font-size: 30px;
    color: #75ff6a;
  }

  .rick-gif {
    width: 200px;

    @media (min-width: 500px) {
    width: 400px;
    }

    @media (min-width: 768px) {
      width: auto;
    }


  }
}
